* {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  margin: 0;
  font-family: "Inter", sans-serif;
}

main {
  height: 100vh;
  display: flex;
  flex-flow: column;
}
.canvas {
  /* height: 100%; */
  flex-grow: 1;
  width: 100%;
  background-color: #ccc;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
  text-align: center;
}

/* Input component */

.form-input {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;

  border-radius: 25px;
  margin: auto;
  width: 85%;
  max-width: 1000px;
  min-height: 70vh;
  padding: 10px;

  background: url(./images/mt_fuji.jpeg) no-repeat center center fixed;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
    rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.main-input[type="text"] {
  width: 50%;
  min-width: 100px;
  font-family: "Inter", sans-serif;
  margin: 5px 10px;
  padding: 12px 20px;
  box-sizing: border-box;
  border: 3px solid red;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  outline: none;
}
.main-input:focus[type="text"] {
  border: 4px solid #1976d2;
}

/* loader */
.loader {
  border: 16px solid #f3f3f3;
  border-top: 16px solid #f44336;
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;

  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  justify-content: center;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
/*  weather component */
.weather-container {
  background-color: #ccc;
  flex-grow: 1;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
}
/* On mouse-over, add a deeper shadow */
.weather-card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

.weather-card {
  background-color: white;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  border-radius: 20px;
  padding: 2vh 10px;
  min-width: fit-content;
  width: 10vw;
  max-width: 250px;
  height: auto;
  margin: 10px 1vw;
  font-size: .75rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
  justify-content: space-evenly;
  transition: 0.3s;
  text-align: center;
}
.temp {
  font-size: 1rem;
}
.weather-icon {
  background-color: rgb(158, 192, 185);
  border-radius: 100%;
  margin-top: 10px;
}
